import React, { useState, useEffect } from "react";
import { 
  Container,
  Row,
  Col,
  Jumbotron,
  Badge, 
} from "react-bootstrap";

import UserService from "../services/user.service";

const BoardAdmin = () => {
  const [content, setContent] = useState("");
  const [numUsers, setNumUsers] = useState(0);
  const [numTutos, setNumTutos] = useState(0);

  useEffect(() => {
    UserService.getAdminBoard().then(
      (response) => {
        setContent(response.data.message);
        setNumUsers(response.data.userCount);
        setNumTutos(response.data.tutoCount);
        //console.log(response);
      },
      (error) => {
        const _content =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        setContent(_content);
      }
    );
  }, []);

  return (
    <Container>
      <Row>
        <Jumbotron>
          <h1>Welcome to the Administration section</h1>
          <h3>{content}</h3>
        </Jumbotron>
      </Row>
      <Row>
        <Col xs={12}>
          <header className="indicators-header">
            <h3>{`Latest informations of <IWantCode/>`}</h3>
          </header>
          <div className="indicators-wrapper">
            <div className="indicator">
              Registered Users
              <Badge variant="success">{numUsers}</Badge>  
            </div>
            <hr />
            <div className="indicator">
              Total Tutorials
              <Badge variant="success">{numTutos}</Badge>  
            </div>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default BoardAdmin;
