import React, { useState, useEffect } from "react";
import TutorialDataService from "../../services/tutorial.service";
import { Link } from "react-router-dom";
import {
  Container,
  Row,
  Col,
  Button,
  ListGroup,
  Form,
} from 'react-bootstrap';
import Search from '../Search';

const TutorialsList = () => {
  const [tutorials, setTutorials] = useState([]);
  const [currentTutorial, setCurrentTutorial] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [searchTitle, setSearchTitle] = useState("");

  useEffect(() => {
    retrieveTutorials();
  }, []);

  const onChangeSearchTitle = e => {
    const searchTitle = e.target.value;
    setSearchTitle(searchTitle);
  };

  const retrieveTutorials = () => {
    TutorialDataService.getAll()
      .then(response => {
        setTutorials(response.data);
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  };

  const refreshList = () => {
    retrieveTutorials();
    setCurrentTutorial(null);
    setCurrentIndex(-1);
  };

  const setActiveTutorial = (tutorial, index) => {
    setCurrentTutorial(tutorial);
    setCurrentIndex(index);
  };

  const removeAllTutorials = () => {
    TutorialDataService.removeAll()
      .then(response => {
        console.log(response.data);
        refreshList();
      })
      .catch(e => {
        console.log(e);
      });
  };

  const findByTitle = (event) => {
    event.preventDefault();
    TutorialDataService.findByTitle(searchTitle)
      .then(response => {
        setTutorials(response.data);
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  };

  return (
    <Container>
      <Row className="justify-content-md-center">
        <Col xs="12" md="8">
          <Search
            value={searchTitle}
            onChange= {onChangeSearchTitle}
            onSubmit={findByTitle}
          >
            Search
          </Search>
        </Col>
      </Row>
      <Row>
        <Col xs="12" md="4">
          <h4>Tutorials List</h4>

            <ListGroup className="mb-2">
            {tutorials &&
              tutorials.map((tutorial, index) => (
                <ListGroup.Item
                  active={index === currentIndex}
                  onClick={() => setActiveTutorial(tutorial, index)}
                  key={index}
                >
                  {tutorial.title}
                </ListGroup.Item>
              ))}
          </ListGroup>

          <Button
            variant="danger"
            onClick={removeAllTutorials}
          >
            Remove All
          </Button>
        </Col>
        <Col xs="12" md="8">
        {currentTutorial ? (
          <div>
            <h4>Tutorial {currentTutorial.id}</h4>
            <div>
              <label>
                <strong>Title:</strong>
              </label>{" "}
              {currentTutorial.title}
            </div>
            <div>
              <label>
                <strong>Description:</strong>
              </label>{" "}
              {currentTutorial.description}
            </div>
            <div>
              <label>
                <strong>Created At</strong>
              </label>{" "}
              {currentTutorial.createdAt}
            </div>
            <div>
              <label>
                <strong>Modified At</strong>
              </label>{" "}
              {currentTutorial.modifiedAt}
            </div>
            <div>
              <label>
                <strong>Status:</strong>
              </label>{" "}
              {currentTutorial.published ? "Published" : "Pending"}
            </div>

            <Form.Group>
              <span className="form-text">Actions for this tutorial</span>
              <Link
                to={"/tutorials/" + currentTutorial.id}
                className="btn btn-primary form-control"
              >
                Read
              </Link>
              <Link
                to={"/tutorials/edit/" + currentTutorial.id}
                className="btn btn-warning form-control"
              >
                Edit
              </Link>
            </Form.Group>
          </div>
        ) : (
          <div>
            <br />
            <p>Please select a Tutorial...</p>
          </div>
        )}
        </Col>
      </Row>
    </Container>
  );
};

export default TutorialsList;
