import React from "react";
import { Navbar, Nav, NavLink, NavItem, Dropdown } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faUser,
  faHome,
  faFileAlt,
  faUserTag,
  faUserTie,
  faUserCheck,
  faUserCircle,
  faSignInAlt,
  faSignOutAlt,
  faUserPlus,
  faChartLine,
} from '@fortawesome/free-solid-svg-icons';

function NavBar({ showModeratorBoard, showAdminBoard, currentUser, logO }) {
  return (
    <div>
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Navbar.Brand to="/">IWantCode</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link href={"/home"}>Home <FontAwesomeIcon icon={faHome} /></Nav.Link>
            <Dropdown as={NavItem} id="collasible-dropdown">
              <Dropdown.Toggle as={NavLink} variant="light">
                Dashboard <FontAwesomeIcon icon={faChartLine}/>
              </Dropdown.Toggle>
              <Dropdown.Menu>
                {currentUser && <Dropdown.Item href={"/users/user"}>User Board <FontAwesomeIcon icon={faUser} /></Dropdown.Item>}
                {showModeratorBoard && (
                  <Dropdown.Item href={"/users/mod"}>Moderator Board <FontAwesomeIcon icon={faUserTag} /></Dropdown.Item>
                )}
                {showAdminBoard && (
                  <Dropdown.Item href={"/users/admin"}>Admin Board <FontAwesomeIcon icon={faUserTie} /></Dropdown.Item>
                )}
              </Dropdown.Menu>
            </Dropdown>
          </Nav>

          <Nav className="ml-auto mr-auto">
            <Navbar.Text>Tutorials <FontAwesomeIcon icon={faFileAlt} /></Navbar.Text>
            <Nav.Link href="/tutorials">List</Nav.Link>
            <Nav.Link href="/tutorials/add">Add</Nav.Link>
          </Nav>

          <Nav className="ml-auto mr-2">
            <Dropdown as={NavItem} id="collasible-dropdown">
              <Dropdown.Toggle as={NavLink} variant="light">
                User <FontAwesomeIcon icon={faUserCircle}/>
              </Dropdown.Toggle>
              <Dropdown.Menu>
                {currentUser ? (
                  <div>
                    <Dropdown.Item href="/profile">
                      <FontAwesomeIcon icon={faUserCheck} /> Profile
                    </Dropdown.Item>
                    <Dropdown.Item href="/" onClick={logO}>
                      <FontAwesomeIcon icon={faSignOutAlt} /> Logout
                    </Dropdown.Item>
                  </div>
                ) : (
                  <div>
                    <Dropdown.Item href="/login"><FontAwesomeIcon icon={faSignInAlt} /> Login</Dropdown.Item>
                    <Dropdown.Item href="/signup"><FontAwesomeIcon icon={faUserPlus} /> Register</Dropdown.Item>
                  </div>
                )}
              </Dropdown.Menu>
            </Dropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </div>
  );
}

export default NavBar;
