import React, { useState, useEffect } from "react";
import { Switch, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

import AuthService from "./services/auth.service";

import NavBar from './components/navigation/NavBar';
import Login from "./components/Login";
import Register from "./components/Register";
import Home from "./components/Home";
import Profile from "./components/Profile";
import BoardUser from "./components/BoardUser";
import BoardModerator from "./components/BoardModerator";
import BoardAdmin from "./components/BoardAdmin";

// tutorial components
import AddTutorial from "./components/tutorial/AddTutorial";
import Tutorial from "./components/tutorial/Tutorial";
import TutorialsList from "./components/tutorial/TutorialsList";
// users components
import UserEdit from "./components/user/UserEdit";
import UserList from "./components/user/UserList";

const App = () => {
  const [showModeratorBoard, setShowModeratorBoard] = useState(false);
  const [showAdminBoard, setShowAdminBoard] = useState(false);
  const [currentUser, setCurrentUser] = useState(undefined);

  useEffect(() => {
    const user = AuthService.getCurrentUser();

    if (user) {
      setCurrentUser(user);
      setShowModeratorBoard(user.roles.includes("ROLE_MODERATOR"));
      setShowAdminBoard(user.roles.includes("ROLE_ADMIN"));
    }
  }, []);

  const logOut = () => {
    AuthService.logout();
  };

  return (
    <div>
      <NavBar
        showModeratorBoard={showModeratorBoard}
        showAdminBoard={showAdminBoard} 
        currentUser={currentUser}
        logO={logOut}
      />
      <div className="container mt-3">
        <Switch>
          <Route exact path={["/", "/home"]} component={Home} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/register" component={Register} />
          <Route exact path="/profile" component={Profile} />
          <Route exact path="/users/user" component={BoardUser} />
          <Route exact path="/users/mod" component={BoardModerator} />
          <Route exact path="/users/admin" component={BoardAdmin} />
          <Route exact path="/userslist"><UserList /></Route>
          <Route path="/users/edit/:id"><UserEdit /></Route>
          <Route path="/users/delete/:id" component={BoardAdmin} />
          <Route path={["/tutorials", "/tutorials/list"]} component={TutorialsList} />
          <Route path="/tutorials/add" component={AddTutorial} />
          <Route path="/tutorials/edit/:id"><Tutorial /></Route>
        </Switch>
      </div>
    </div>
  );
};

export default App;
