import React, { useState, useEffect } from "react";
import UserDataService from "../../services/user.service";
import { Link } from "react-router-dom";
import {
  Container,
  Row,
  Col,
  ListGroup,
  ListGroupItem,
  Form,
} from "react-bootstrap";
import Search from "../Search";

const UserList = () => {
  const [users, setUsers] = useState([]);
  const [currentUser, setCurrentUser] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [searchName, setSearchName] = useState("");

  useEffect(() => {
    retrieveUsers();
  }, []);

  const onChangeSearchName = (e) => {
    const searchName = e.target.value;
    setSearchName(searchName);
  };

  const retrieveUsers = () => {
    UserDataService.getAllUsers()
      .then((response) => {
        setUsers(response.data);
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
        console.log("retrive pas putain");
      });
  };

  const setActiveUser = (user, index) => {
    setCurrentUser(user);
    setCurrentIndex(index);
  };

  const findUserByName = (event) => {
    event.preventDefault();
    UserDataService.findByName(searchName)
      .then((response) => {
        setUsers(response.data);
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  return (
    <Container>
      <Row className="justify-content-md-center">
        <Col xs="12" md="8">
          <Search
            value={searchName}
            onChange={onChangeSearchName}
            onSubmit={findUserByName}
          >
            Search
          </Search>
        </Col>
      </Row>
      <Row>
        <Col xs={12} md={4}>
          <h4>Users List</h4>

          <ListGroup className="mb-2">
            {users &&
              users.map((user, index) => (
                <ListGroup.Item
                  active={index === currentIndex}
                  onClick={() => setActiveUser(user, index)}
                  key={index}
                >
                  {user.username}
                </ListGroup.Item>
              ))}
          </ListGroup>
        </Col>
        <Col xs={12} md={8}>
          {currentUser ? (
            <div>
              <h4>User informations</h4>
              <div className="form-inline">
                <label style={{ with: "30%" }}>
                  <strong className="mr-1">Username:</strong>
                  {currentUser.username}
                </label>
              </div>
              <div>
                <label style={{ with: "30%" }}>
                  <strong className="mr-1">EMail:</strong>
                  {currentUser.email}
                </label>
              </div>
              <div>
                <label style={{ with: "30%" }}>
                  <strong className="mr-1">Public</strong>
                  {currentUser.public ? "true" : "false"}
                </label>
              </div>
              <div>
                <label style={{ with: "30%" }}>
                  <strong className="mr-1">Registered At:</strong>
                  {currentUser.createdAt}
                </label>
              </div>
              <div>
                <label style={{ with: "30%" }}>
                  <strong className="mr-1">Authorities:</strong>
                </label>
                <ListGroup>
                  {currentUser.roles &&
                    currentUser.roles.map((role, index) => (
                      <ListGroupItem key={index}>{role}</ListGroupItem>
                    ))}
                </ListGroup>
              </div>

              <Form.Group>
                <span className="form-text">Actions for this User</span>
                <Link
                  to={"/users/edit/" + currentUser.id}
                  className="btn btn-warning"
                >
                  Edit
                </Link>
              </Form.Group>
            </div>
          ) : (
            <div>
              <br />
              <p>Please select a User...</p>
            </div>
          )}
        </Col>
      </Row>
    </Container>
  );
};

export default UserList;
