import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import TutorialDataService from "../../services/tutorial.service";
import { Container, Form, Row, Col, Button } from "react-bootstrap";

const Tutorial = (props) => {
  const initialTutorialState = {
    id: null,
    title: "",
    description: "",
    content: "",
    published: false,
  };
  const params = useParams();
  const [currentTutorial, setCurrentTutorial] = useState(initialTutorialState);
  const [message, setMessage] = useState("");

  const getTutorial = (id) => {
    TutorialDataService.get(id)
      .then((response) => {
        setCurrentTutorial(response.data);
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  useEffect(() => {
    getTutorial(params.id);
  }, [params.id]);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setCurrentTutorial({ ...currentTutorial, [name]: value });
  };

  const handleSwitchChange = (event) => {
    const etat = event.target.checked;
    updatePublished(etat);
  };

  const updatePublished = (status) => {
    var data = {
      id: currentTutorial.id,
      title: currentTutorial.title,
      description: currentTutorial.description,
      content: currentTutorial.content,
      published: status,
    };

    TutorialDataService.update(currentTutorial.id, data)
      .then((response) => {
        setCurrentTutorial({ ...currentTutorial, published: status });
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const updateTutorial = () => {
    TutorialDataService.update(currentTutorial.id, currentTutorial)
      .then((response) => {
        console.log(response.data);
        setMessage("The tutorial was updated successfully!");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const deleteTutorial = () => {
    TutorialDataService.remove(currentTutorial.id)
      .then((response) => {
        console.log(response.data);
        props.history.push("/tutorials");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  return (
    <Container>
      <Row className="justify-content-md-center">
        <Col sm={12} md={10} lg={8} xl={6}>
          {currentTutorial ? (
            <div>
              <h4 className="text-center">Tutorial</h4>
              <Form>
                <Form.Group as={Row}>
                  <Form.Label column sm={3} htmlFor="title">
                    Title
                  </Form.Label>
                  <Col sm={9}>
                    <Form.Control
                      type="text"
                      id="title"
                      required
                      value={currentTutorial.title}
                      onChange={handleInputChange}
                      name="title"
                    />
                  </Col>
                </Form.Group>

                <Form.Group as={Row}>
                  <Form.Label column sm={3} htmlFor="description">
                    Description
                  </Form.Label>
                  <Col sm={9}>
                    <Form.Control
                      type="text"
                      id="description"
                      required
                      value={currentTutorial.description}
                      onChange={handleInputChange}
                      name="description"
                    />
                  </Col>
                </Form.Group>

                <Form.Group as={Row}>
                  <Form.Label column sm={3} htmlFor="content">
                    Content
                  </Form.Label>
                  <Col sm={9}>
                    <Form.Control
                      as="textarea"
                      placeholder="Type your content here ..."
                      rows="8"
                      id="content"
                      required
                      value={currentTutorial.content}
                      onChange={handleInputChange}
                      name="content"
                    />
                  </Col>
                </Form.Group>

                <Form.Group as={Row}>
                  <Form.Label column sm={3} htmlFor="published">
                    Tutorial published state :
                  </Form.Label>
                  <Col sm={9}>
                    <Form.Check
                      type="switch"
                      id="published"
                      label={
                        currentTutorial.published ? "Published" : "Pending"
                      }
                      checked={currentTutorial.published}
                      onChange={handleSwitchChange}
                      name="published"
                    />
                  </Col>
                </Form.Group>
              </Form>

              <Form.Group className="text-center">
                <Button
                  type="submit"
                  className="btn btn-success mr-2"
                  onClick={updateTutorial}
                >
                  Update
                </Button>

                <Button
                  className="btn btn-danger"
                  onClick={deleteTutorial}
                >
                  Delete
                </Button>
              </Form.Group>

              <p>{message}</p>
            </div>
          ) : (
            <Form.Group className="text-center">
              <br />
              <p>Please select a Tutorial...</p>
              <Link
                to={"/tutorials/"}
                className="btn btn-success"
              >
                Back to list
              </Link>
            </Form.Group>
          )}
        </Col>
      </Row>
    </Container>
  );
};

export default Tutorial;
